<template>
  <main class="invite">
    <section
      class="nofinish"
      v-if="!user.name || !user.lastname || !user.trida"
    >
      <span
        ><span class="bold"
          >et nem podstatn informace ke sprvn funknosti!</span
        >
        Doplte svoje daje v sti
        <router-link to="/dashboard/profil/upravit"
          >"Profil > Upravit Profil"</router-link
        ></span
      >
    </section>
    <template>
      <transition name="inv" mode="out-in">
        <section class="loading" v-if="tab == 'loading'" key="loading">
          <i class="fas fa-circle-notch"></i>
        </section>
        <section class="join" v-else-if="tab == 'join'" key="join">
          <h1>Pozvnka</h1>
          <h2><i :class="typeIcon"></i> {{ type }}</h2>
          <div
            class="game-icon"
            :style="`background-image: url(${api(`${game.iconURL}`)})`"
          ></div>
          <h3>{{ team.tag }}  {{ team.title }}</h3>
          <button @click="joinTeam" v-if="!joinLoading">Pijmout</button>
          <button v-else class="invalid">
            <i class="fas fa-circle-notch"></i>
          </button>
        </section>
        <section class="nickname" v-else-if="tab == 'nickname'" key="nickname">
          <h1>V nick ve he:</h1>
          <div
            class="game-icon"
            :style="`background-image: url(${api(`${game.iconURL}`)})`"
          ></div>
          <input type="text" v-model="nickname" placeholder="Nickname" />
          <button
            @click="sendNick"
            :class="nickInvalid ? 'invalid' : ''"
            v-if="!nickLoading"
          >
            Odeslat
          </button>
          <button v-else class="invalid">
            <i class="fas fa-circle-notch"></i>
          </button>
        </section>
        <section class="already" v-else-if="tab == 'already'" key="already">
          <h1>V tomhle tmu u dvno jste.</h1>
          <router-link :to="`/dashboard/tymy/${team.slug}`"
            >Otevt Tm</router-link
          >
        </section>
      </transition>
    </template>
  </main>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    team: null,
    invite: null,
    game: null,
    invalidInvite: false,
    joinLoading: false,
    nickname: "",
    nickLoading: false,
    tab: "loading",
  }),
  computed: {
    user() {
      return this.$store.state.user;
    },
    type() {
      if (this.invite.type == "player") return "Hr";
      else if (this.invite.type == "mod") return "Sprvce";
      else return null;
    },
    typeIcon() {
      if (this.invite.type == "player") return "fas fa-user";
      else if (this.invite.type == "mod") return "fas fa-tools";
      else return null;
    },
    nickInvalid() {
      return !this.nickname;
    },
  },
  async created() {
    try {
      let res = await axios.get(
        this.apiUrl(`/invite/${this.$route.params.code}`),
        {
          headers: {
            Authorization: `Bearer ${this.$store.state.token}`,
          },
        }
      );
      console.log(res.data);
      this.team = res.data.team;
      this.invite = res.data.invite;
      this.game = res.data.game;
      if (!res.data.hasGame && res.data.invite.type !== "mod")
        this.tab = "nickname";
      else this.tab = "join";
    } catch (error) {
      if (error.response.status === 403) {
        this.team = error.response.data.team;
        if (error.response.data.why == "isOwner")
          this.$router.push(`/dashboard/tymy/${this.team.slug}`);
        this.tab = "already";
      }
      this.invalidInvite = true;
    }
  },
  methods: {
    api(url) {
      return this.apiUrl(url);
    },
    async sendNick() {
      if (!this.nickname) return;
      this.nickLoading = true;
      try {
        await axios.post(
          this.apiUrl(`/user/game`),
          {
            nickname: this.nickname,
            game: this.game.id,
          },
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.nickLoading = false;
        this.tab = "join";
      } catch (error) {
        console.log(error);
        this.nickLoading = false;
      }
    },
    async joinTeam() {
      this.joinLoading = true;
      try {
        await axios.post(
          this.apiUrl(`/invite/${this.invite.code}/join`),
          {},
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.joinLoading = false;
        this.$router.push(`/dashboard/tymy/${this.team.slug}`);
      } catch (error) {
        console.log(error);
        this.joinLoading = false;
      }
    },
  },
};
</script>

