<template>
  <section class="view team-edit" v-if="team && !noTeam">
    <div class="top">
      <div class="route">
        <span>/</span>
        <router-link :to="`/dashboard/tymy/${team.slug}`">{{
          team.title
        }}</router-link>
        <span>/</span>
        <span>Upravit</span>
      </div>
      <router-link :to="`/dashboard/tymy/${team.slug}`" class="back"
        >Zpt</router-link
      >
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    team: null,
    noTeam: null,
  }),
  created() {
    this.getTeam();
  },
  methods: {
    async getTeam() {
      try {
        let res = await axios.get(
          this.apiUrl(`/dash/team/${this.$route.params.slug}`),
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.team = res.data;
      } catch (error) {
        this.noTeam = true;
      }
    },
  },
};
</script>

