<template>
  <main class="turnaj">
    <template v-if="tourney && !noTourney">
      <section class="banner">
        <div
          class="banner"
          :style="`background-image: url(${tourney.data.bannerURL})`"
        ></div>
        <div class="darken-banner"></div>
        <div class="content-wrap">
          <div class="content">
            <div class="route">
              <span>/</span>
              <router-link to="/turnaje">Turnaje</router-link>
              <span>/</span>
              <span>{{ tourney.title }}</span>
            </div>
            <div class="data">
              <h1>{{ tourney.title }}</h1>
              <div class="details">
                <div class="detail">
                  <span class="name">Hra</span>
                  <div>
                    <i class="fas fa-gamepad"></i>
                    <span>{{ tourney.data.game }}</span>
                  </div>
                </div>
                <div class="detail">
                  <span class="name">Prize Pool</span>
                  <div>
                    <i class="fas fa-trophy"></i>
                    <span>{{ tourney.prizepool.total }}k</span>
                  </div>
                </div>
                <div class="detail">
                  <span class="name">Vstupn</span>
                  <div>
                    <i class="fas fa-user-plus"></i>
                    <span>{{ tourney.prizepool.enter }}k</span>
                  </div>
                </div>
                <div class="detail">
                  <span class="name">Datum</span>
                  <div>
                    <i class="fas fa-calendar-day"></i>
                    <span>{{ tourney.data.start }}</span>
                  </div>
                </div>
                <div class="detail">
                  <span class="name">Tmy</span>
                  <div>
                    <i class="fas fa-users"></i>
                    <span
                      >{{ tourney.data.teams.registered }}/{{
                        tourney.data.teams.max
                      }}</span
                    >
                  </div>
                </div>
              </div>
            </div>
            <!--<div class="actions">
              <a v-if="!user" class="register" @click="redirectLogin">
                Registrace
              </a>
              <router-link
                v-else
                class="register"
                :to="`/turnaje/${tourney.slug}/registrace`"
              >
                Registrace
              </router-link>
            </div>-->
          </div>
        </div>
      </section>
      <section class="tabs">
        <router-link :to="`/turnaje/${tourney.slug}`" exact>
          Pehled
        </router-link>
        <!--<router-link :to="`/turnaje/${tourney.slug}`" exact>
          Brackets
        </router-link>
        <router-link :to="`/turnaje/${tourney.slug}`" exact>
          Zpasy
        </router-link>
        <router-link :to="`/turnaje/${tourney.slug}`" exact> Tmy </router-link>-->
        <router-link :to="`/turnaje/${tourney.slug}/registrace`">
          Registrace
        </router-link>
      </section>

      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </template>
    <template v-if="!tourney && !noTourney">
      <section class="loading">
        <i class="fas fa-circle-notch"></i>
      </section>
    </template>
    <template v-if="noTourney">
      <section class="not-found">
        <span>Nic jsme nenali :(</span>
        <router-link to="/turnaje">Zpt na vechny turnaje</router-link>
      </section>
    </template>
  </main>
</template>
 
<script>
import axios from "axios";
export default {
  data: () => ({
    tourney: null,
    noTourney: false,
  }),
  computed: {
    user() {
      return this.$store.state.user;
    },
  },
  async created() {
    this.getTourney();
  },
  methods: {
    async getTourney() {
      try {
        let res = await axios.get(
          this.apiUrl(`/tourney/${this.$route.params.slug}`)
        );
        this.tourney = res.data;
      } catch (error) {
        this.noTourney = true;
      }
    },
    redirectLogin() {
      localStorage.setItem(
        "prestiz_redirect",
        `/turnaje/${this.$route.params.slug}/registrace`
      );
      this.$router.push("/login");
    },
  },
};
</script>

<style>
</style>