<template>
  <section class="view team-invite" v-if="team && !noTeam">
    <div class="top">
      <div class="route">
        <span>/</span>
        <router-link :to="`/dashboard/tymy/${team.slug}`">{{
          team.title
        }}</router-link>
        <span>/</span>
        <span>Pozvnka</span>
      </div>
      <router-link :to="`/dashboard/tymy/${team.slug}`" class="back"
        >Zpt</router-link
      >
    </div>
    <div class="team-invite">
      <div class="section">
        <h2>Koho chcete pozvat?</h2>
        <label>
          <input type="radio" v-model="type" name="type" value="player" />
          Hre
        </label>
        <label v-if="isOwner">
          <input type="radio" v-model="type" name="type" value="mod" />
          Sprvce
        </label>
      </div>
      <div class="section">
        <h2>Poet pouit pozvnky</h2>
        <div class="row">
          <button
            class="change-uses"
            :class="uses == 1 ? 'disabled' : ''"
            @click="subUse"
          >
            <i class="fas fa-minus"></i>
          </button>
          <input type="number" v-model="uses" />
          <button
            class="change-uses"
            :class="uses == 10 ? 'disabled' : ''"
            @click="addUse"
          >
            <i class="fas fa-plus"></i>
          </button>
        </div>
      </div>
      <div class="section">
        <button
          class="submit"
          @click="getInvite"
          v-if="!submitLoading && !invite"
        >
          Vytvoit Pozvnku
        </button>
        <button class="submit loading" v-else-if="!submitLoading && invite">
          <i class="fas fa-check"></i> Kliknte na odkaz pro zkoprovn
        </button>
        <button class="submit loading" v-else>
          <i class="fas fa-circle-notch"></i>
        </button>
      </div>
      <div class="section">
        <button class="link" v-clipboard="() => invite" v-if="invite">
          {{ invite }}
        </button>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    team: null,
    noTeam: false,
    type: "player",
    uses: 5,
    submitLoading: false,
    invite: "",
  }),
  async created() {
    await this.getTeam();
    if (!this.isMod) this.$router.push("/dashboard/tymy");
  },
  computed: {
    isOwner() {
      return this.$store.state.user.teams.find((t) => t.id === this.team.id)
        .owner;
    },
    isMod() {
      return this.$store.state.user.teams.find((t) => t.id === this.team.id)
        .mod;
    },
  },
  methods: {
    async getTeam() {
      try {
        let res = await axios.get(
          this.apiUrl(`/dash/team/${this.$route.params.slug}`),
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.team = res.data;
      } catch (error) {
        this.noTeam = true;
      }
    },
    addUse() {
      let newUses = parseInt(this.uses) + 1;
      if (newUses > 10) return;
      this.uses = newUses;
    },
    subUse() {
      let newUses = parseInt(this.uses) - 1;
      if (newUses < 1) return;
      this.uses = newUses;
    },
    async getInvite() {
      this.submitLoading = true;
      try {
        let res = await axios.post(
          this.apiUrl(`/dash/team/${this.$route.params.slug}/invite`),
          {
            type: this.type,
            uses: this.uses,
          },
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.invite = res.data.invite;
        this.submitLoading = false;
      } catch (error) {
        console.log(error);
        this.uses = Math.round(this.uses);
        if (this.uses > 10) this.uses = 10;
        if (this.uses < 1) this.uses = 1;
        this.submitLoading = false;
      }
    },
  },
  watch: {
    type(to) {
      if (to == "player") {
        this.uses = "5";
      }
      if (to == "mod") {
        this.uses = "1";
      }
    },
  },
};
</script>

