<template>
  <main class="login">
    <div class="background"></div>
    <div class="bg-darken"></div>
    <section class="content-wrap">
      <div class="content">
        <div class="info">
          <h2></h2>
          <h3></h3>
        </div>
        <div class="form">
          <transition name="login" mode="out-in">
            <div class="login-form" v-if="loginTab === 'login'" key="login">
              <h2>Pihlen</h2>
              <div class="input-wrap" v-show="loginTab === 'login'">
                <input
                  type="email"
                  placeholder="V koln email"
                  v-model="email"
                  :class="emailError ? 'error' : ''"
                  @click="emailError = false"
                />
                <span
                  class="input-error"
                  v-show="emailError"
                  v-html="emailErrorText"
                ></span>
                <input
                  type="password"
                  placeholder="Jakkoliv heslo (min. 8 znak)"
                  v-model="password"
                  :class="passError ? 'error' : ''"
                  @click="passError = false"
                />
                <span
                  class="input-error"
                  v-show="passError"
                  v-html="passErrorText"
                  >Nesprvn heslo.</span
                >
              </div>
              <div class="keep-login" @click="save = !save">
                <i :class="save ? 'fas fa-check-square' : 'far fa-square'"></i>
                <span>Zstat pihlen (Na 14 dn)</span>
              </div>

              <button v-if="!loginLoading" @click="submitLogin">
                Pihlsit se
              </button>
              <button v-else class="loading">
                <i class="fas fa-circle-notch"></i>
              </button>
              <span class="code-alrdy" @click="loginTab = 'code'"
                >U mm kd</span
              >
            </div>
            <div class="login-form" v-if="loginTab === 'code'" key="code">
              <h2>Ovovac Kd</h2>
              <h3>
                Pi registraci abychom ovili zda jste to opravdu vy, jsme vm
                poslali ovovac kd na va emailovou adresu.
              </h3>
              <div class="input-wrap">
                <input
                  type="text"
                  placeholder="V koln email"
                  v-model="email"
                />
                <span
                  class="input-error"
                  v-show="emailError"
                  v-html="emailErrorText"
                ></span>
                <input type="text" placeholder="Ovovac kd" v-model="code" />
              </div>
              <button v-if="!codeLoading" @click="submitCode">Odeslat</button>
              <button v-else class="loading">
                <i class="fas fa-circle-notch"></i>
              </button>
            </div>
            <div class="login-form" v-if="loginTab === 'data'" key="data">
              <h2>Dokonen registrace</h2>
              <div class="input-wrap">
                <input type="text" placeholder="Kestn jmno" v-model="name" />
                <input type="text" placeholder="Pjmen" v-model="lastname" />
                <input type="text" placeholder="Tda" v-model="trida" />
              </div>
              <button v-if="!dataLoading" @click="submitData">
                Dokonit Registraci
              </button>
              <button v-else class="loading">
                <i class="fas fa-circle-notch"></i>
              </button>
            </div>
          </transition>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    loginTab: "login",
    email: "",
    emailError: false,
    emailErrorText: "",
    password: "",
    passError: false,
    passErrorText: "",
    save: false,
    loginLoading: false,
    code: "",
    codeLoading: false,
    name: "",
    lastname: "",
    trida: "",
    dataLoading: false,
  }),
  methods: {
    async submitLogin() {
      this.loginLoading = true;
      try {
        let res = await axios.post(this.apiUrl("/login"), {
          email: this.email,
          password: this.password,
        });
        res.data.save = this.save;
        this.$store.dispatch("login", res.data);
        let redirect = localStorage.getItem("prestiz_redirect");
        if (!redirect) this.$router.push("/dashboard");
        else {
          localStorage.removeItem("prestiz_redirect");
          this.$router.push(redirect);
        }
      } catch (error) {
        let message = error.response.data.replaceAll(/<[^>]*>?/gm, "");
        this.loginLoading = false;
        switch (error.response.status) {
          case 401:
            this.loginTab = "code";
            break;
          case 403:
            this.passError = true;
            this.passErrorText = message;
            break;
          case 400:
            this.emailError = true;
            this.emailErrorText = message;
            break;
          default:
            break;
        }
      }
    },
    async submitCode() {
      this.codeLoading = true;
      try {
        let res = await axios.post(this.apiUrl("/register"), {
          email: this.email,
          code: this.code,
        });
        res.data.save = this.save;
        this.$store.dispatch("login", res.data);
        this.loginTab = "data";
        this.loginLoading = false;
      } catch (error) {
        let message = error.response.data.replaceAll(/<[^>]*>?/gm, "");
        switch (error.response.status) {
          case 401:
            this.loginTab = "login";
            break;
          case 400:
            this.emailError = true;
            this.emailErrorText = message;
            break;
        }
      }
    },
    async submitData() {
      this.dataLoading = true;
      try {
        let res = await axios.post(
          this.apiUrl("/user-data"),
          {
            name: this.name,
            lastname: this.lastname,
            trida: this.trida,
          },
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.dataLoading = false;
        this.$store.commit("setUser", res.data);
        let redirect = localStorage.getItem("prestiz_redirect");
        if (!redirect) this.$router.push("/dashboard");
        else {
          localStorage.removeItem("prestiz_redirect");
          this.$router.push(redirect);
        }
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

