<template>
  <section class="registrace">
    <h1>Vyberte si tm:</h1>
    <div class="teams">
      <article class="team chosen" v-for="t in noUsableTeams" :key="t.id">
        <i class="far fa-circle"></i>
        <span class="name">{{ t.title }}</span>
        <div class="grow"></div>
        <i class="fas fa-chevron-up"></i>
      </article>
      <article class="team no-use" v-for="t in usableTeams" :key="t.id">
        <i class="fas fa-ban"></i>
        <span class="name">{{ t.title }}</span>
        <div class="grow"></div>
        <span> &lt; 5 hr</span>
      </article>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    teams: null,
  }),
  computed: {
    usableTeams() {
      if (this.teams) return this.teams.filter((t) => t.players.length >= 5);
      else return [];
    },
    noUsableTeams() {
      if (this.teams) return this.teams.filter((t) => t.players.length < 5);
      else return [];
    },
  },
  created() {
    this.getTeams();
  },
  methods: {
    async getTeams() {
      try {
        let res = await axios.get(this.apiUrl("/user/tourney-teams"), {
          headers: {
            Authorization: `Bearer ${this.$store.state.token}`,
          },
        });
        this.teams = res.data;
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

