<template>
  <section class="view profile-games">
    <div class="top">
      <div class="route">
        <span>/</span><router-link to="/dashboard">Dashboard</router-link
        ><span>/</span><router-link to="/dashboard/profil">Profil</router-link
        ><span>/</span><span>Hry</span>
      </div>
    </div>
    <div class="games">
      <p>Kliknutm na hru ji pidte do svch hranch her!</p>
      <div class="select-games">
        <div
          class="s-game"
          v-for="game in games"
          :key="game.id"
          :style="`background-image: url(${api(`${game.iconURL}`)})`"
          :class="activeGames.includes(game.id) ? 'chosen' : ''"
        ></div>
      </div>
      <div class="game" v-for="g in user.games" :key="g.id">
        <div class="info">
          <div
            class="game-img"
            :style="`background-image: url(${api(`${getGameIcon(gid)}`)})`"
          ></div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    games: null,
    api: Function,
  }),
  computed: {
    user() {
      return this.$store.state.user;
    },
    activeGames() {
      return this.$store.state.user.games.map((g) => g.id);
    },
  },
  created() {
    this.getGames();
    this.api = this.apiUrl;
  },
  methods: {
    async getGames() {
      let res = await axios.get(this.apiUrl("/games"));
      this.games = res.data;
    },
    getGameIcon(id) {
      return this.games.find((g) => g.id === id).iconURL;
    },
  },
};
</script>