import Dashboard from '../../views/Dashboard/Dashboard.vue'
import DashboardOverview from '../../views/Dashboard/DashboardOverview.vue'
import DashboardProfile from '../../views/Dashboard/DashboardProfile.vue'
import DashboardProfileEdit from '../../views/Dashboard/DashboardProfileEdit.vue'
import DashboardProfileGames from '../../views/Dashboard/DashboardProfileGames.vue'
import DashboardTeams from '../../views/Dashboard/DashboardTeams.vue'
import DashboardTeamsCreate from '../../views/Dashboard/DashboardTeamsCreate.vue'
import DashboardTeam from '../../views/Dashboard/DashboardTeam.vue'
import DashboardTeamInvite from '../../views/Dashboard/DashboardTeamInvite.vue'
import DashboardTeamEdit from '../../views/Dashboard/DashboardTeamEdit.vue'

export default
  {
    path: '/dashboard',
    component: Dashboard,
    children: [
      {
        path: "",
        name: "Dashboard",
        component: DashboardOverview,
        meta: { requiresAuth: true },
      },
      {
        path: "tymy",
        name: "DashboardTeams",
        component: DashboardTeams,
        meta: { requiresAuth: true },
      },
      {
        path: "tymy/novy",
        name: "DashboardTeamsCreate",
        component: DashboardTeamsCreate,
        meta: { requiresAuth: true },
      },
      {
        path: "tymy/:slug",
        name: "DashboardTeam",
        component: DashboardTeam,
        meta: { requiresAuth: true },
      },
      {
        path: "tymy/:slug/invite",
        name: "DashboardTeamInvite",
        component: DashboardTeamInvite,
        meta: { requiresAuth: true },
      },
      {
        path: "tymy/:slug/upravit",
        name: "DashboardTeamEdit",
        component: DashboardTeamEdit,
        meta: { requiresAuth: true },
      },
      {
        path: "profil",
        name: "DashboardProfie",
        component: DashboardProfile,
        meta: { requiresAuth: true },
      },
      {
        path: "profil/upravit",
        name: "DashboardProfieEdit",
        component: DashboardProfileEdit,
        meta: { requiresAuth: true },
      },
      {
        path: "profil/games",
        name: "DashboardProfieGames",
        component: DashboardProfileGames,
        meta: { requiresAuth: true },
      }
    ]
  }
