<template>
  <section class="view team-create">
    <div class="top">
      <div class="route">
        <span>/</span>
        <router-link to="/dashboard">Dashboard</router-link>
        <span>/</span>
        <router-link to="/dashboard/tymy">Moje Tmy</router-link>
        <span>/</span>
        <span>Vytvoen Tmu</span>
      </div>
      <a
        class="submitTeam"
        v-if="!submitLoading"
        :class="valid ? '' : 'invalid'"
        @click="submitTeam"
        >Vytvoit</a
      >
      <a
        class="submitTeam loading"
        v-else
        :class="valid ? '' : 'invalid'"
        @click="submitTeam"
        ><i class="fas fa-circle-notch"></i
      ></a>
    </div>
    <div class="create">
      <!--<div class="icon-part">
        <h2>Logo Tmu</h2>
        <div class="icon-wrap">
          <label for="iconInput" v-if="!icon">
            <i class="far fa-file-image" v-if="!icon"></i>
            <span>*.png</span>
          </label>
          <div
            class="icon"
            :style="`background-image: url('${iconDataUrl}')`"
            v-else
            @click="removeIcon"
          ></div>
        </div>
        <input
          type="file"
          ref="iconInput"
          @change="uploadFile($event)"
          accept=".png"
          id="iconInput"
        />
      </div>-->
      <div class="inputs">
        <h1>Vytvoen Tmu</h1>
        <span class="error"></span>
        <div class="input-group">
          <input
            type="text"
            v-model="title"
            :class="titleError ? 'error' : ''"
            placeholder="Nzev tmu"
            @click="titleError = false"
          />
          <span
            class="input-error"
            v-show="titleError"
            v-html="titleErrorMessage"
          ></span>
          <input
            type="text"
            v-model="tag"
            :class="tagError ? 'error' : ''"
            placeholder="Zkratka tmu"
            @click="tagError = false"
          />
          <span
            class="input-error"
            v-show="tagError"
            v-html="tagErrorMessage"
          ></span>
          <select v-model="game" @click="gameError = false">
            <option disabled value="">Vyberte hru</option>
            <option v-for="g in games" :key="g.id" :value="g.id">
              {{ g.title }}
            </option>
          </select>
          <span
            class="input-error"
            v-show="gameError"
            v-html="'Vyberte hru!'"
          ></span>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    //icon: null,
    title: "",
    titleError: false,
    titleErrorMessage: "",
    tag: "",
    tagError: false,
    tagErrorMessage: "",
    game: "",
    gameError: false,
    games: [],
    submitLoading: false,
  }),
  computed: {
    user() {
      return this.$store.state.user;
    },
    valid() {
      let invalid = this.title === "" || this.tag === "" || this.game === "";
      return !invalid;
    },
    /*iconDataUrl() {
      return URL.createObjectURL(this.icon);
    },*/
  },
  methods: {
    /*uploadFile(event) {
      this.icon = event.target.files[0];
    },
    removeIcon() {
      this.icon = null;
    },*/
    async submitTeam() {
      if (this.title === "") {
        this.titleError = true;
        this.titleErrorMessage = "Zadejte nzev tmu!";
      }
      if (this.tag === "") {
        this.tagError = true;
        this.tagErrorMessage = "Zadejte zkratku tmu!";
      }
      if (this.game === "") this.gameError = true;
      if (!this.valid) return;
      this.submitLoading = true;
      try {
        let res = await axios.post(
          this.apiUrl("/team/create"),
          {
            title: this.title,
            tag: this.tag,
            game: this.game,
          },
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.submitLoading = false;
        this.$router.push(`/dashboard/tymy/${res.data.slug}`);
      } catch (error) {
        this.submitLoading = false;
        console.log(error.response.data);
        switch (error.response.status) {
          case 403:
            if (error.response.data.type == "alert") {
              alert(error.response.data.message);
              this.$router.push("/dashboard/tymy");
            } else if (error.response.data.type == "title") {
              this.titleError = true;
              this.titleErrorMessage = error.response.data.message;
            } else if (error.response.data.type == "tag") {
              this.tagError = true;
              this.tagErrorMessage = error.response.data.message;
            }
            break;
          case 400:
            console.log(error);
            break;
        }
      }
    },
  },
  async created() {
    let res = await axios.get(this.apiUrl("/games"));
    this.games = res.data;
  },
};
</script>

