Vue.prototype.getUser = async () => {
  if (store.state.token) {
    try {
      let res = await axios.get(Vue.prototype.apiUrl("/user"), {
        headers: {
          'Authorization': `Bearer ${store.state.token}`
        }
      })
      store.commit("setUser", res.data);
      return res.data;
    } catch (err) {
      localStorage.removeItem("prestiz_token");
      store.dispatch("login", { token: null, user: null });
      return null;
    }
  }
  else return null;
};

router.beforeEach(async (to, from, next) => {
  let loggedIn = await Vue.prototype.getUser()
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!loggedIn) {
      localStorage.setItem("prestiz_redirect", to.fullPath);
      next("/login")
    } else {
      next()
    }
  } else {
    next()
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
