<template>
  <section class="view profile-edit">
    <div class="top">
      <div class="route">
        <span>/</span>
        <router-link to="/dashboard">Dashboard</router-link>
        <span>/</span>
        <router-link to="/dashboard/profil">Profil</router-link>
        <span>/</span>
        <span>Upravit</span>
      </div>
      <button @click="submitEdit" :class="invalid ? 'invalid' : ''">
        Uloit
      </button>
    </div>
    <div class="edit">
      <input type="text" v-model="name" placeholder="Kestn jmno" />
      <input type="text" v-model="lastname" placeholder="Pijmen" />
      <input type="text" v-model="trida" placeholder="Tda" />
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    name: "",
    lastname: "",
    trida: "",
  }),
  computed: {
    user() {
      return this.$store.state.user;
    },
    invalid() {
      return !this.name || !this.lastname || !this.trida;
    },
  },
  created() {
    this.name = this.user.name;
    this.lastname = this.user.lastname;
    this.trida = this.user.trida;
  },
  methods: {
    async submitEdit() {
      if (this.invalid) return;
      try {
        await axios.post(
          this.apiUrl("/user/edit"),
          {
            name: this.name,
            lastname: this.lastname,
            trida: this.trida,
          },
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.$router.push("/dashboard/profil");
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

