<template>
  <section class="view teams">
    <div class="top">
      <div class="route">
        <span>/</span>
        <router-link to="/dashboard">Dashboard</router-link>
        <span>/</span>
        <span>Moje Tmy</span>
      </div>
      <router-link class="createTeam" to="/dashboard/tymy/novy"
        >Vytvoit Tm</router-link
      >
    </div>
    <div class="teams">
      <span v-if="teams.length === 0" class="empty">
        Nejste v dnm tmu :(</span
      >
      <template v-else>
        <router-link
          v-for="t in teams"
          :key="t.id"
          :to="`/dashboard/tymy/${t.slug}`"
        >
          <div class="details">
            <div class="role">
              <i class="fas fa-crown" v-if="t.owner"></i>
              <span class="title" v-if="t.owner">Majitel</span>
              <i class="fas fa-tools" v-if="t.mod && !t.owner"></i>
              <span class="title" v-if="t.mod && !t.owner">Sprvce</span>
              <i class="fas fa-user" v-if="t.player"></i>
              <span class="title" v-if="t.player">Hr</span>
            </div>
          </div>
          <div
            class="game"
            :style="`background-image: url(${api(
              `/imgs/games/${t.game}/icon.png`
            )})`"
          ></div>
          <div class="data">
            <span class="tag">{{ t.tag }}</span>
            <span>
              {{ t.title }}
            </span>
          </div>
        </router-link>
      </template>
    </div>
  </section>
</template>

<script>
export default {
  data: () => ({
    api: Function,
  }),
  computed: {
    teams() {
      return this.$store.state.user.teams;
    },
  },
  created() {
    this.api = this.apiUrl;
  },
};
</script>

