import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import Turnaje from '../views/Turnaje.vue'
import Turnaj from '../views/Turnaj/Turnaj.vue'
import TurnajOverview from '../views/Turnaj/TurnajOverview.vue'
import TurnajRegistrace from '../views/Turnaj/TurnajRegistrace.vue'
import Invite from '../views/Invite.vue'


import dashboard from "./parts/dashboard";

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/o-nas',
    name: 'O ns',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/login',
    name: "Pihlen",
    component: Login
  },
  {
    path: '/i/:code',
    name: "Invite",
    component: Invite,
    meta: { requiresAuth: true },
  },
  {
    path: '/turnaje',
    name: "Turnaje",
    component: Turnaje
  },
  {
    path: '/turnaje/:slug',
    component: Turnaj,
    children: [
      {
        path: "",
        name: "Turnaj",
        component: TurnajOverview
      },
      {
        path: "registrace",
        component: TurnajRegistrace,
        meta: { requiresAuth: true },
      }
    ]
  }
]

routes.push(dashboard);

const router = new VueRouter({
  routes
})

export default router
