<template>
  <main class="turnaje">
    <section class="banner">
      <div class="banner"></div>
      <div class="darken-banner"></div>
      <div class="title-wrap">
        <div class="title">
          <h1>Turnaje</h1>
        </div>
      </div>
    </section>
    <div class="main">
      <section>
        <h2>Nadchzejc turnaje</h2>
        <div class="articles">
          <transition name="fade">
            <article v-for="t in upcoming" :key="t.id">
              <div
                class="banner"
                :style="`background-image: url(${t.data.bannerURL})`"
              ></div>
              <div class="content">
                <h3>{{ t.title }}</h3>
                <div class="info">
                  <div class="properties">
                    <span>Datum</span>
                    <span>Tmy</span>
                  </div>
                  <div class="values">
                    <span>{{ t.data.start }}</span>
                    <span
                      >{{ t.data.teams.registered }}/{{
                        t.data.teams.max
                      }}</span
                    >
                  </div>
                </div>
                <div class="actions">
                  <router-link :to="`/turnaje/${t.slug}`" class="view"
                    >Zobrazit</router-link
                  >
                  <router-link
                    :to="`/turnaje/${t.slug}/registrace`"
                    class="register"
                    v-if="user"
                  >
                    Registrace
                  </router-link>
                </div>
              </div>
            </article>
          </transition>
        </div>
      </section>
      <section>
        <h2>Pedel turnaje</h2>
        <div class="articles">
          <span class="empty"> Nikde nic. </span>
        </div>
      </section>
    </div>
  </main>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    upcoming: [],
    finished: [],
  }),

  async created() {
    let res = await axios.get(this.apiUrl("/tourneys"));
    this.upcoming = res.data.upcoming;
    this.finished = res.data.finished;
  },
  computed: {
    user() {
      return this.$store.state.user;
    },
  },
};
</script>

