<template>
  <section class="view team" v-if="team && !noTeam">
    <div class="top">
      <div class="route">
        <span>/</span>
        <router-link to="/dashboard/tymy">Moje Tmy</router-link>
        <span>/</span>
        <span>{{ team.title }}</span>
      </div>
      <div class="buttons">
        <!--<a class="del" v-if="isOwner">
          <i class="fas fa-trash"></i>
        </a>-->
        <router-link
          :to="`/dashboard/tymy/${team.slug}/upravit`"
          class="edit"
          v-if="isMod"
          ><i class="fas fa-pencil-alt"></i
        ></router-link>
        <router-link
          :to="`/dashboard/tymy/${team.slug}/invite`"
          class="invite"
          v-if="isMod"
        >
          <i class="fas fa-user-plus"></i>
        </router-link>
      </div>
    </div>
    <div class="section-team">
      <div class="section">
        <h1>{{ team.title }}</h1>
        <div class="team-details">
          <span>{{ team.tag }}</span>
          <span></span>
          <span>{{ team.game }}</span>
        </div>
      </div>
      <div class="section">
        <h2>Uivatel</h2>
        <div class="players">
          <div class="player">
            <div class="name">
              <i class="fas fa-crown"></i>
              <span> {{ owner.name }} {{ owner.lastname }} </span>
              <span class="trida"> {{ owner.trida }} </span>
            </div>
            <div class="actions">
              <button
                v-if="isOwner && !owner.player"
                class="yellow"
                @click="ownerPlayer(true)"
              >
                <i class="fas fa-user-plus"></i>
              </button>
            </div>
          </div>
          <div class="player" v-for="a in mods" :key="a.email">
            <div class="name">
              <i class="fas fa-tools"></i>
              <span> {{ a.name }} {{ a.lastname }} </span>
              <span class="trida"> {{ a.trida }} </span>
            </div>
            <button v-if="isOwner" @click="removeMod(a.email)">
              <i class="fas fa-user-times"></i>
            </button>
          </div>
          <div class="hr" />
          <template v-if="players.length > 0">
            <div class="player" v-for="p in players" :key="p.email">
              <div class="name">
                <i class="fas fa-user"></i>
                <span> {{ p.name }} {{ p.lastname }} </span>
                <span class="trida">{{ p.trida }}</span>
                <span class="darken">{{ p.nickname }}</span>
              </div>
              <div class="actions">
                <button
                  v-if="isOwner && !p.owner && !p.mod"
                  class="make-mod"
                  @click="makeMod(p.email)"
                >
                  <i class="fas fa-tools"></i>
                </button>
                <button
                  v-if="isMod && !p.owner"
                  class="del"
                  @click="delPlayer(p.email)"
                >
                  <i class="fas fa-user-times"></i>
                </button>
                <button v-if="p.owner" class="del" @click="ownerPlayer(false)">
                  <i class="fas fa-user-minus"></i>
                </button>
              </div>
            </div>
          </template>
          <span v-else>dn hri </span>
        </div>
      </div>
      <div class="section" v-if="isMod">
        <h2>Pozvnky</h2>
        <div class="invites">
          <div class="invite" v-for="i in team.invites" :key="i.code">
            <div class="data">
              <i class="fas fa-tools" v-if="i.type == 'mod'"></i>
              <i class="fas fa-user" v-else-if="i.type == 'player'"></i>
              <span class="code">{{ i.code }}</span>
              <span class="uses"
                ><span class="darken">Pouito: </span
                >{{ i.maxUses - i.uses }}/{{ i.maxUses }}</span
              >
            </div>
            <div class="actions">
              <button class="copy" v-clipboard="() => i.link">
                <i class="fas fa-clipboard"></i>
              </button>
              <button
                class="del"
                @click="delInv(i.code)"
                v-if="isOwner || (isMod && i.type == 'player')"
              >
                <i class="fas fa-trash"></i>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    originalTeam: null,
    team: null,
    noTeam: false,
  }),
  async created() {
    this.getTeam();
  },
  computed: {
    isOwner() {
      return this.$store.state.user.teams.find((t) => t.id === this.team.id)
        .owner;
    },
    isMod() {
      return this.$store.state.user.teams.find((t) => t.id === this.team.id)
        .mod;
    },
    players() {
      return this.team.users.filter((u) => u.player === true);
    },
    mods() {
      return this.team.users.filter((u) => u.mod === true && u.owner === false);
    },
    owner() {
      return this.team.users.find((u) => u.owner === true);
    },
  },
  methods: {
    async getTeam() {
      try {
        let res = await axios.get(
          this.apiUrl(`/dash/team/${this.$route.params.slug}`),
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.originalTeam = res.data;
        this.team = res.data;
      } catch (error) {
        this.noTeam = true;
      }
    },
    async delInv(code) {
      let c = confirm(`Chcete opravdu smazat pozvnku ${code}?`);
      if (!c) return;
      try {
        await axios.delete(
          this.apiUrl(`/dash/team/${this.team.slug}/invite/${code}`),
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.getTeam();
      } catch (error) {
        console.log(error);
      }
    },
    async delPlayer(email) {
      let p = this.players.find((pl) => pl.email === email);
      if (!p) return;
      let c = confirm(
        `Chcete opravdu vyhodit hre ${p.name} ${p.lastname} z tmu?`
      );
      if (!c) return;
      if (p.mod && !p.owner) {
        try {
          await axios.post(
            this.apiUrl(`/dash/team/${this.$route.params.slug}/make`),
            {
              email,
              what: "player",
              bool: false,
            },
            {
              headers: {
                Authorization: `Bearer ${this.$store.state.token}`,
              },
            }
          );
          this.getTeam();
        } catch (error) {
          console.log(error);
        }
      } else if (!p.mod && !p.owner) {
        try {
          await axios.delete(
            this.apiUrl(`/dash/team/${this.team.slug}/user/${email}`),
            {
              headers: {
                Authorization: `Bearer ${this.$store.state.token}`,
              },
            }
          );
          this.getTeam();
        } catch (error) {
          console.log(error);
        }
      }
    },
    async makeMod(email) {
      try {
        await axios.post(
          this.apiUrl(`/dash/team/${this.$route.params.slug}/make`),
          {
            email,
            what: "mod",
            bool: true,
          },
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.getTeam();
      } catch (error) {
        console.log(error);
      }
    },
    async removeMod(email) {
      let m = this.mods.find((mod) => mod.email === email);
      if (!m) return;
      let c = confirm(
        `Chcete opravdu odebrat sprvce uivateli ${m.name} ${m.lastname}?`
      );
      if (!c) return;
      if (m.player) {
        try {
          await axios.post(
            this.apiUrl(`/dash/team/${this.$route.params.slug}/make`),
            {
              email,
              what: "mod",
              bool: false,
            },
            {
              headers: {
                Authorization: `Bearer ${this.$store.state.token}`,
              },
            }
          );
          this.getTeam();
        } catch (error) {
          console.log(error);
        }
      } else {
        try {
          await axios.delete(
            this.apiUrl(`/dash/team/${this.team.slug}/user/${email}`),
            {
              headers: {
                Authorization: `Bearer ${this.$store.state.token}`,
              },
            }
          );
          this.getTeam();
        } catch (error) {
          console.log(error);
        }
      }
    },
    async ownerPlayer(bool) {
      if (!this.owner.nickname && bool) {
        let nickname = prompt(
          `Nemte nastaven v nickname ve he ${this.team.game}, kter je pro pipojen mezi hre poteba.\nV nickname:`
        );
        if (nickname === null) {
          return;
        } else {
          await axios.post(
            this.apiUrl(`/user/game`),
            {
              nickname,
              game: this.team.game,
            },
            {
              headers: {
                Authorization: `Bearer ${this.$store.state.token}`,
              },
            }
          );
        }
      }
      try {
        await axios.post(
          this.apiUrl(`/dash/team/${this.$route.params.slug}/make`),
          {
            email: this.owner.email,
            what: "player",
            bool,
          },
          {
            headers: {
              Authorization: `Bearer ${this.$store.state.token}`,
            },
          }
        );
        this.getTeam();
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

