import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: null,
    user: null
  },
  mutations: {
    setToken(state, token) {
      state.token = token;
    },
    setUser(state, user) {
      state.user = user;
    }
  },
  actions: {
    login(store, data) {
      store.commit("setToken", data.token);
      store.commit("setUser", data.user);
      if (data.save) {
        localStorage.setItem("prestiz_token", data.token);
      }
    }
  },
  modules: {
  }
})
